@charset "UTF-8";

@font-face {
  font-family: Arial;
  font-display: swap;
  src: url("../fonts/Arial-Regular.woff") format("woff"), url("../fonts/Arial-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: BebasNeue;
  font-display: swap;
  src: url("../fonts/BebasNeue-Regular.woff") format("woff"), url("../fonts/BebasNeue-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  overflow-wrap: break-word;
  user-select: text;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-style: normal;
  background-color: #ffffff;
}

body::-webkit-scrollbar {
  width: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  max-width: 1440px;
  padding: 0 90px;
  margin: 0 auto;
  box-sizing: border-box;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.header {
  width: 100%;
  min-height: 70px;
  background: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
  position: relative;
  z-index: 200;
}

.header .containers {
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 30px;
  flex-wrap: nowrap;
  width: 100%;
}

.header__menu {
  display: none;
  width: 45px;
  cursor: pointer;
  height: 45px;
}

.header__block {
  width: max-content;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
}

.header__block:first-child {
  justify-content: flex-start;
  gap: 32px;
}

.header__block:last-child {
  justify-content: flex-end;
  gap: 30px;
}

.header__logo {
  cursor: pointer;
}

.header__subtitle {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #262626;
  font-style: normal;
}

.header__email {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 11px;
  flex-wrap: nowrap;
}

.header__email p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
  letter-spacing: -0.5px;
}

.header__phone {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 11px;
  flex-wrap: nowrap;
}

.header__phone p {
  font-weight: 400;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
}

.header__button {
  background: #f2ce14;
  border-radius: 10px;
  padding: 10px 17px;
  cursor: pointer;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: #141413;
  font-style: normal;
  text-transform: uppercase;
}

.headerCitySelect {
  padding: 7px 5px 7px 25px;
  border-radius: 30.5px;
  background-color: #ffffff;
  width: 75px;
  position: relative;
  box-sizing: content-box;
}

.headerCitySelect::after {
  content: "";
  background: url("./../svg/header/arrow-down-blue-dark.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 50%;
  right: -8px;
  transform: translate(0, -50%);
  height: 11px;
  width: 14px;
}

.headerCitySelect::before {
  content: "";
  width: 24px;
  height: 24px;
  position: absolute;
  top: 50%;
  left: 0;
  background-image: url("./../svg/header/geo.svg");
  transform: translate(0, -50%);
}

.headerCitySelect-placeholder {
  display: block;
  padding: 5px 0;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
  font-weight: 500;
  font-size: 15px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  white-space: nowrap;
  box-sizing: content-box;
}

.headerCitySelect-block {
  bottom: auto;
  top: 100%;
  position: absolute;
  left: 0;
  z-index: 5;
  box-sizing: content-box;
}

.headerCitySelect-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0px;
  flex-wrap: nowrap;
  height: max-content;
  max-height: 120px;
  overflow-y: auto;
}

.headerCitySelect-wrapper::-webkit-scrollbar {
  width: 27px;
}

.headerCitySelect-wrapper::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #808080;
  box-shadow: inset 0 0 10px 10px #808080;
  border: solid 12px transparent;
}

.headerCitySelect-wrapper::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #141413;
  box-shadow: inset 0 0 10px 10px #141413;
  border: solid 10px transparent;
}

.headerCitySelect-element {
  width: 100%;
  padding: 3px 0;
  background: 0 0;
  color: #141413;
  border: none;
  margin: 0 0 5px 0;
  text-align: left;
  cursor: pointer;
}

.headerCitySelect-element-active {
  background: #ffffff;
}

.headerCitySelect-item-active::after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.headerCitySelect-item-active .headerCitySelect-block {
  padding: 15px 0 15px 15px;
  width: 100px;
  height: auto;
  border-radius: 15px;
  background-color: #ffffff;
  border: 1px solid #141413;
}

.navbar {
  position: relative;
  z-index: 160;
  width: 100%;
  background-color: #f1f2ed;
}

.navbar .containers {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  gap: 28px;
  flex-wrap: nowrap;
}

.navbar__background {
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  cursor: not-allowed;
  visibility: hidden;
  height: 100vh;
  background: #140848;
  z-index: 100;
  transition: all 0.5s ease;
}

.navbar__background-active {
  opacity: 0.9;
  cursor: auto;
  visibility: visible;
}

.navbar__search {
  margin: 16px 0;
  width: 305px;
  background: #ffffff;
  border-radius: 19px;
  padding: 7px 18px 7px 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 10px;
  flex-wrap: nowrap;
}

.navbar__search-input {
  width: 100%;
  background-color: transparent;
  font-style: normal;
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 17px;
  line-height: 19px;
  color: #141413;
  font-style: normal;
}

.navbar__search-input::placeholder {
  font-weight: 400;
  font-size: 17px;
  line-height: 19px;
  color: #141413;
  font-style: normal;
  opacity: 0.5;
}

.navbar__search-button {
  width: 24px;
  height: 24px;
  background-color: transparent;
  cursor: pointer;
}

.navbar__link {
  cursor: pointer;
}

.navbar__link p {
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 18px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
}

.navbar__arrow {
  padding: 25px 0;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  gap: 12px;
  flex-wrap: nowrap;
}

.navbar__arrow p {
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 18px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
}

.navbar__arrow img {
  width: 19px;
  height: 19px;
}

.navbar__arrow-block {
  position: fixed;
  left: 0;
  top: 0;
  padding: 140px 0 0 0;
  cursor: not-allowed;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease;
  background-color: #ffffff;
  width: 100%;
  height: 100vh;
  z-index: 120;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;
  gap: 0px;
  flex-wrap: nowrap;
}

.navbar__arrow-block__active {
  cursor: auto;
  visibility: visible;
  opacity: 1;
}

.navbar__arrow-catalog {
  width: 340px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0px;
  flex-wrap: nowrap;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
}

.navbar__arrow-catalog::-webkit-scrollbar {
  width: 0;
}

.navbar__arrow-content {
  width: calc(100% - 340px);
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;
  gap: 25px;
  flex-wrap: wrap;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
}

.navbar__arrow-con {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: row;
  gap: 25px;
  flex-wrap: wrap;
  display: none;
  padding: 25px 25px 25px 65px;
}

.navbar__arrow-con__active {
  display: flex;
}

.navbar__arrow-nav {
  width: 100%;
  padding: 29px 25px 29px 80px;
  background-color: #140848;
  transition: all 0.5s ease;
  cursor: pointer;
}

.navbar__arrow-nav a {
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  font-style: normal;
}

.navbar__arrow-nav__active {
  background: #40395a;
}

.navbar__arrow-containers {
  width: max-content;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 25px;
  flex-wrap: nowrap;
}

.navbar__arrow-title {
  font-weight: 700;
  font-size: 22px;
  line-height: 25px;
  color: #140848;
  font-style: normal;
}

.navbar__arrow-links {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 12px;
  flex-wrap: nowrap;
}

.navbar__arrow-button {
  cursor: pointer;
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 17px;
  line-height: 49px;
  color: #140848;
  font-style: normal;
  transition: all 0.5s ease;
}

.navbar__arrow-button::hover a {
  text-decoration: underline;
}

.navbar__arrow-button a {
  letter-spacing: -0.5px;
  font-weight: 400;
  font-size: 17px;
  line-height: 49px;
  color: #140848;
  font-style: normal;
  transition: all 0.5s ease;
}

.navbar__arrow-list {
  width: 235px;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 0px;
  flex-wrap: nowrap;
  position: absolute;
  z-index: 120;
  top: 69px;
  left: -30px;
  cursor: not-allowed;
  visibility: hidden;
  opacity: 0;
  transition: all 0.5s ease;
}

.navbar__arrow-list__active {
  opacity: 1;
  cursor: auto;
  visibility: visible;
}

.navbar__arrow-item {
  cursor: pointer;
  padding: 28px;
  letter-spacing: -0.5px;
  background-color: #ffffff;
  transition: all 0.5s ease;
  width: 100%;
}

.navbar__arrow-item a {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #140848;
  font-style: normal;
  transition: all 0.5s ease;
}

.navbar__arrow-item:hover {
  background: #40395a;
}

.navbar__arrow-item:hover a {
  color: #ffffff;
}

.footer {
