.formBase {
  background-color: #ffffff;
  padding: 100px 0 140px 0;
  width: 100%;
}

.formBase .containers {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 25px;
  flex-wrap: nowrap;
  width: 100%;
}

.formBase__title {
  font-weight: 700;
  font-size: 40px;
  line-height: 58px;
  color: #201547;
  font-style: normal;
}

.formBase__container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 50px;
  width: 100%;
}

.formBase__banner {
  width: 396px;
  margin: 31px 0 0 0;
  height: 500px;
  border-radius: 20px;
  overflow: hidden;
}

.formBase__banner img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.formBase__form {
  position: relative;
}

.formBase__form-banner {
  position: absolute;
  z-index: 1;
  left: 0;
  bottom: 0;
  width: 432px;
  height: 67px;
  border: 2px solid #f2ce14;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2px;
  flex-wrap: nowrap;
}

.formBase__form-banner span {
  font-weight: 700;
  font-size: 17px;
